import React, { useContext } from 'react'
import icon from "./assets/dropdown-icon.png"
import { LanguageContext } from "./LanguageContext"
import style from "./modules.css/Language.module.css"

export default function Language() {

    const codeToLanguage = {
        "it-IT": "Italiano",
        "en-US": "English"
    }

    const languageInUse = useContext(LanguageContext)[0]
    const change = useContext(LanguageContext)[1];
    
    function setLanguage(newLanguage) {
        change(newLanguage)
    }
    return (
        <>

            <ul className={style.menu}>
                <li>
                    <div className={style.languagePreviewContainer}>
                        <p className={style.languagePreviewText}>{codeToLanguage[languageInUse]}</p>
                        <img src={icon} alt="change language" style={{height: "20px"}} />
                    </div>

                    <ul>
                        <li className={style.dropdownElement} onClick={() => { setLanguage("it-IT") }}><p>Italiano</p></li>
                        <li className={style.dropdownElement} onClick={() => { setLanguage("en-US") }}><p>English</p></li>
                    </ul>
                </li>
            </ul>
        </>
    )
}