import React, { useState } from 'react'
import {
    BrowserRouter as Router,
    Route,
} from "react-router-dom";
import SearchMovies from "./Search/SearchMovies"
import MovieDetails from './MovieDetails';
import TopBar from "./TopBar/TopBar"
import "./modules.css/App.module.css"
import TopRated from "./TopBar/TopRated"
import Upcoming from "./TopBar/Upcoming"
import Popular from "./TopBar/Popular"
import LanguageProvider from "./LanguageContext"

function App() {

    const [keyword, setKeyword] = useState("");
    

    return (
        <LanguageProvider>
            <Router>
                <Route path="/easyMovies/"><TopBar onChange={setKeyword} /></Route>
                <Route exact path="/easyMovies/" component={Upcoming}></Route>
                <Route path="/easyMovies/search"><SearchMovies keyword={keyword} /></Route>
                <Route exact path="/easyMovies/toprated" component={TopRated} />
                <Route exact path="/easyMovies/upcoming" component={Upcoming} />
                <Route exact path="/easyMovies/popular" component={Popular} />
                <Route exact path="/easyMovies/MovieDetails/:id" component={MovieDetails} />
            </Router>
        </LanguageProvider>
    );
}

export default App;