import React, { useState, useEffect, useContext } from 'react';
import Axios from 'axios';
import DisplayMovies from '../DisplayMovies';
import Loading from '../Loading';
import {LanguageContext} from "../LanguageContext"

export default function TopRated() {

    const [isLoading, setIsLoading] = useState(true);
    const [movies, setMovies] = useState([]);
    const language = useContext(LanguageContext)[0]

    useEffect(() => {
        setIsLoading(true)
        Axios.get(`https://api.themoviedb.org/3/movie/top_rated?api_key=4ed037f9ad7d3a3826a4a561283af97d&language=${language}&page=1`).then(res => {
            setMovies(res.data.results)
            setIsLoading(false)
        })
    }, [language]);

    return (<>
        {isLoading ? <Loading /> : <DisplayMovies movies={movies} />}
    </>
    )
}