import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import topBarStyle from "../modules.css/TopBar.module.css"
import SearchBar from '../Search/SearchBar';
import Language from '../Language';
import { LanguageContext } from "../LanguageContext"
import sideBarStyle from "../modules.css/SideBar.module.css"
import bars from "../assets/bars.png"


export default function TopBar(props) {

    const language = useContext(LanguageContext)[0];
    const size = useWindowSize();
    const [version, setVersion] = useState("TOPBAR");

    const [showSideBar, setShowSideBar] = useState(true);

    useEffect(() => {
        if (size.width < 1000)
            setVersion("SIDEBAR")
        else
            setVersion("TOPBAR")
            setShowSideBar(false)
    }, [size])

    function sideBar(){
        setShowSideBar(!showSideBar)
    }

    return (
        <>
            {
                version === "TOPBAR" ?
                    <ul className={topBarStyle.ul}>
                        <Link to="/easyMovies/toprated">
                            <li className={`${topBarStyle.topBarElement} ${topBarStyle.sideBarElement}`}>{language === "en-US" ? "Top Rated" : "I più votati"}</li>
                        </Link>
                        <Link to="/easyMovies/upcoming">
                            <li className={`${topBarStyle.topBarElement} ${topBarStyle.sideBarElement}`}>{language === "en-US" ? "Upcoming" : "In uscita"}</li>
                        </Link>
                        <Link to="/easyMovies/popular">
                            <li className={`${topBarStyle.topBarElement} ${topBarStyle.sideBarElement}`}>{language === "en-US" ? "Popular" : "Di tendenza"}</li>
                        </Link>
                        <SearchBar onChange={props.onChange} />
                        <li className={`${topBarStyle.topBarElement} ${topBarStyle.sideBarElement}`}><Language /></li>
                    </ul>
                    :
                    <ul className={`${sideBarStyle.topBar}`}>
                        <li className={sideBarStyle.li}>
                            <img src={bars} alt="menu" className={sideBarStyle.bars} onClick={sideBar} />
                            <div className={sideBarStyle.SearchBarContainer}><SearchBar onChange={props.onChange} /></div>

                        </li>
                    </ul>
            }
            {
                showSideBar &&
                <ul className={sideBarStyle.optionsContainer}>
                    <Link to="/easyMovies/toprated">
                        <li className={topBarStyle.sideBarElement}><p className={sideBarStyle.sideBarLink}>{language === "en-US" ? "Top Rated" : "I più votati"}</p></li>
                    </Link>
                    <Link to="/easyMovies/upcoming">
                        <li className={topBarStyle.sideBarElement}><p className={sideBarStyle.sideBarLink}>{language === "en-US" ? "Upcoming" : "In uscita"}</p></li>
                    </Link>
                    <Link to="/easyMovies/popular">
                        <li className={topBarStyle.sideBarElement}><p className={sideBarStyle.sideBarLink}>{language === "en-US" ? "Popular" : "Di tendenza"}</p></li>
                    </Link>
                    <li className={topBarStyle.sideBarElement}><div className={sideBarStyle.sideBarLink}><Language mode="SIDEBAR"/></div></li>
                </ul>
            }
        </>

    )
}

function useWindowSize() {
    const isClient = typeof window === 'object';

    function getSize() {
        return {
            width: isClient ? window.innerWidth : undefined,
            height: isClient ? window.innerHeight : undefined
        };
    }

    const [windowSize, setWindowSize] = useState(getSize);

    useEffect(() => {
        if (!isClient) {
            return false;
        }

        function handleResize() {
            setWindowSize(getSize());
        }

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }); // Empty array ensures that effect is only run on mount and unmount
    return windowSize;
}