import React, { useState, useEffect, useContext } from 'react';
import Axios from 'axios';
import DisplayMovies from '../DisplayMovies';
import Loading from '../Loading';
import { LanguageContext } from "../LanguageContext"

var _ = require('lodash');

let a = _.debounce((setMovies, setIsLoading, language, keyword)=> {
    console.log("in useeffect")
    Axios.get(`https://api.themoviedb.org/3/search/movie?api_key=4ed037f9ad7d3a3826a4a561283af97d&language=${language}&query=${keyword}&page=1&include_adult=false`).then((res) => {
        setMovies(res.data.results);
        setIsLoading(false)
    })
}, 300)

function SearchMovies(props) {
    const [isLoading, setIsLoading] = useState(true);
    const [movies, setMovies] = useState([]);
    const language = useContext(LanguageContext)[0]
    const [hasKeyword, setHasKeyword] = useState(false)
    

    useEffect(() => {
        if (props.keyword === "") {
            setIsLoading(false)
            setHasKeyword(false)
            return
        }
        setIsLoading(true)
        setHasKeyword(true)
        a(setMovies, setIsLoading, language, props.keyword)
    }, [props.keyword, language]);

    return (
        <>
            {hasKeyword ? (
                isLoading ? <Loading /> : <DisplayMovies movies={movies} />)
                :
                <p>.</p>
            }
        </>
    );
}

export default SearchMovies;