import React, { useState, useEffect, useContext } from 'react'
import Axios from 'axios';
import "./App"
import style from "./modules.css/MovieDetails.module.css"
import Ratings from './Ratings/Ratings';
import DisplayMovies from './DisplayMovies';
import { LanguageContext } from "./LanguageContext"

export default function MovieDetails({ match }) {
    var _ = require('lodash');

    const [movie, setMovie] = useState({});
    const [recommendations, setRecommendations] = useState([]);

    const [isLoadingMovies, setIsLoadingMovies] = useState(true)
    const [isLoadingRecommendations, setIsLoadingRecommendations] = useState(true)
    const language = useContext(LanguageContext)[0]

    useEffect(() => {
        setIsLoadingMovies(true)
        setIsLoadingRecommendations(true)
        Axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}?api_key=4ed037f9ad7d3a3826a4a561283af97d&language=${language}`).then((res) => {
            let tmp = {};
            console.log(res.data.title)
            tmp.title = _.get(res.data, "title", language === "en-US" ? "Unknown title" : "Titolo sconosciuto");
            tmp.runtime = _.get(res.data, "runtime", language === "en-US" ? "unknown" : "sconosciuta");
            tmp.release_date = _.get(res.data, "release_date", language === "en-US" ? "unknown" : "sconosciuta");
            tmp.vote_average = _.get(res.data, "vote_average", -1);
            tmp.overview = _.get(res.data, "overview", language === "en-US" ? "Unknown overview" : "Trama sconosciuta");
            tmp.genres = _.get(res.data, "genres", []);
            tmp.poster_path = _.get(res.data, "poster_path");
            setMovie(tmp)
            setIsLoadingMovies(false)
        })
        Axios.get(`https://api.themoviedb.org/3/movie/${match.params.id}/recommendations?api_key=4ed037f9ad7d3a3826a4a561283af97d&language=${language}&page=1`).then((res) => {
            setRecommendations(_.get(res, "data.results", []));
            setIsLoadingRecommendations(false)
        })

    }, [match.params.id, language, _])

    const months = ["January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"];

    const mesi = ["Gennaio", "Febbraio", "Marzo", "Aprile", "Maggio", "Giugno",
        "Luglio", "Agosto", "Settembre", "Ottobre", "Novembre", "Dicembre"];

    function showDate(date) {
        let m = language === "it-IT" ? mesi[date.substr(5, 2)] : months[date.substr(5, 2)]
        return date.substr(-2) + " " + m + " " + date.substr(0, 4)
    }

    return (
        <>
            {!isLoadingMovies &&
                <div className={style.container}>
                    <div className={style.movie}>
                        <img className={style.poster} src={"https://image.tmdb.org/t/p/original/" + movie.poster_path} alt={movie.title} />
                        {/* <ReactImageAppear
                            src={"https://image.tmdb.org/t/p/original/" + movie.poster_path}
                            animation="zoomIn"
                            animationDuration="0.2s"
                            className={style.poster}
                            showLoader={false}
                        /> */}
                        <div className={style.details}>
                            <p className={style.title}>{movie.title}</p>
                            <div className={style.data}>
                                <p>{language === "en-US" ? "Runtime" : "Durata"}: {movie.runtime} {language === "en-US" ? "minutes" : "Durata"}</p>
                                <p>{language === "en-US" ? "Release date" : "Uscita"}: {showDate(movie.release_date)}</p>
                                <p>
                                    <Ratings score={movie.vote_average} />
                                </p>
                                {/* <p>{movie.vote_count} votes.</p> */}
                            </div>

                            <p className={style.overview}>{movie.overview}</p>
                            <div className={style.genres}>
                                {movie.genres.map(g => { return g.name }).join(", ")}
                            </div>
                        </div>
                    </div>
                    {!isLoadingRecommendations &&
                        <div className={style.recommendations}>
                            <DisplayMovies movies={recommendations} />
                        </div>}
                </div>
            }
        </>
    );
};