import React from 'react';
import style from "./modules.css/DisplayMovies.module.css"
import { Link } from 'react-router-dom';
import ReactImageAppear from 'react-image-appear';

function Movie(props) {
    const m = props.movie

    return (
        <Link to={"/easyMovies/MovieDetails/" + m.id} style={{ textDecoration: 'none' }}>
            <div className={style.movieContainer}>
                {/* <img
                    className={style.poster}
                    src={"https://image.tmdb.org/t/p/w500/" + m.poster_path}
                    alt={m.title}
                /> */}
                <ReactImageAppear
                    src={"https://image.tmdb.org/t/p/w500/" + m.poster_path}
                    animation="zoomIn"
                    animationDuration="0.2s"
                    className={style.poster}
                    showLoader={false}
                />
                <div className={style.gradient}></div>
                <p className={style.title}>{m.title}</p>
            </div>
        </Link>
    )
}

export default function DisplayMovies(props) {
    return (
        <div className={style.movies}>
            {props.movies.map(m => <Movie movie={m} key={m.id} />)}
        </div>
    )
}