import React from 'react'; 
import empty_star from './empty_star.png'; 
import full_star from './full_star.png';

function Star(props) {
    return (
        <img src={props.full ? empty_star : full_star} alt={props.score + ""} style={{height: "25px", padding: "2px"}}/>
    );
}

export default function Ratings(props) {
    let res = []
    for (let i = 0; i < 5; i++) {
        res.push(<Star full={props.score/2 < i} score={props.score/2} key={i} />);
    }
    return res;
}